=\"name_admin\" required />\n          </div>\n          <div class=\"form-group\">\n            <label for=\"mdp_admin\">Mot de passe</label>\n            <input v-model=\"mdp_admin\" type=\"password\" id=\"mdp_admin\" required />\n          </div>\n          <button type=\"submit\" class=\"login-button\">Se connecter</button>\n        </form>\n        <p v-if=\"errorMessage\" class=\"error-message\">{{ errorMessage }}</p>\n      </div>\n    </div>\n  </template>\n  \n  <script setup>\n  import { ref } from 'vue'\n  import axios from 'axios'\n  \n  const id_admin = ref('')\n  const name_admin = ref('')\n  const mdp_admin = ref('')\n  const errorMessage = ref('')\n  \n  const handleLogin = async () => {\n    try {\n      const response = await axios.post('/securedashboard', {\n        id_admin: id_admin.value,\n        name_admin: name_admin.value,\n        mdp_admin: mdp_admin.value,\n      })\n  \n      if (response.data.success) {\n        window.location.href = '/dashboard'\n      } else {\n        errorMessage.value = 'Identifiants incorrects. Veuillez réessayer.'\n      }\n    } catch (error) {\n      errorMessage.value = 'Erreur lors de la connexion. Veuillez réessayer.'\n      console.error(error)\n    }\n  }\n  </script>\n  \n  <style scoped>\n  .secure-dashboard {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    min-height: 100vh;\n    background-color: #f5f7fa;\n  }\n  \n  .form-container {\n    background: white;\n    padding: 2rem;\n    border-radius: 1rem;\n    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);\n    width: 100%;\n    max-width: 400px;\n  }\n  \n  h2 {\n    text-align: center;\n    margin-bottom: 1.5rem;\n  }\n  \n  .form-group {\n    margin-bottom: 1rem;\n  }\n  \n  label {\n    display: block;\n    font-weight: 600;\n    margin-bottom: 0.5rem;\n  }\n  \n  input {\n    width: 100%;\n    padding: 0.6rem;\n    border: 1px solid #ccc;\n    border-radius: 0.5rem;\n  }\n  \n  .login-button {\n    width: 100%;\n    padding: 0.7rem;\n    background-color: #1d4ed8;\n    color: white;\n    border: none;\n    border-radius: 0.5rem;\n    font-weight: 600;\n    cursor: pointer;\n    transition: background-color 0.3s ease;\n  }\n  \n  .login-button:hover {\n    background-color: #2563eb;\n  }\n  \n  .error-message {\n    color: red;\n    text-align: center;\n    margin-top: 1rem;\n  }\n  </style>\n  "],"sourceRoot":""}]);
